:root {
  scroll-behavior: smooth;
  ---orange-main: var(---orange-main);
}

*, :before, :after {
  box-sizing: border-box;
}

.h1, .h2, .h3, .h4, .h5, .h6, .p {
  margin: 0;
}

.body {
  width: 100%;
  min-width: 320px;
  height: 7206px;
  background: #fafafa;
  margin: 0 auto;
  font-family: Montserrat, Arial, Helvetica, sans-serif;
}

.body::-webkit-scrollbar {
  display: none;
}

.footer {
  background-color: #1e1823;
  padding: 32px 0;
  display: block;
}

.footer-container {
  width: 335px;
  margin-left: auto;
  margin-right: auto;
}

.footer-section {
  flex-direction: column;
  gap: 32px;
  margin-bottom: 32px;
  display: flex;
}

.footer-title {
  width: 300px;
  color: #fff;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
}

.footer-color {
  color: #fd9222;
}

.addresses {
  justify-content: space-between;
  display: flex;
}

.addresses-title {
  color: #fff;
  margin-bottom: 14px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.3;
}

.address-links {
  text-align: center;
  letter-spacing: -.02em;
  color: #fff;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.3;
  text-decoration: none;
  transition: color .4s cubic-bezier(.4, 0, .2, 1);
}

.address-links:not(:last-child) {
  margin-bottom: 4px;
}

.address-links:hover, .address-links:focus {
  color: #fd9222;
}

.addresses-items:not(:last-child) {
  margin-bottom: 4px;
}

.address {
  font-style: italic;
}

.p {
  margin-top: 1em;
  margin-left: 0;
  margin-right: 0;
  display: block;
}

.ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.li, .addresses-items, .footer-social-items, .footer-navigation-items {
  list-style: none;
  display: list-item;
  position: relative;
}

.a {
  color: currentColor;
  text-decoration: none;
}

.a:-webkit-any-link {
  color: -webkit-link;
  cursor: pointer;
}

.social-list, .footer-social-list {
  gap: 18px;
  display: flex;
}

.footer-social-link:hover, .footer-social-link:focus {
  color: #fd9222;
}

.footer-social-link {
  width: 22px;
  height: 22px;
  border-radius: 25%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.social-icon {
  color: #fff;
  transition: stroke .4s cubic-bezier(.4, 0, .2, 1);
}

.footer-social-link:hover .social-icon, .footer-social-link:focus .social-icon {
  stroke: #fd9222;
}

.div {
  display: block;
}

.footer-line {
  border-image-source: initial;
  border-image-slice: initial;
  border-image-width: initial;
  border-image-outset: initial;
  border-image-repeat: initial;
  border: 1px solid rgba(255, 255, 255, .1);
  margin-bottom: 32px;
}

.footer-navigation-items:not(:last-child) {
  margin-bottom: 18px;
}

.footer-navigation-link {
  color: #fff;
  padding-top: 24px;
  padding-bottom: 24px;
  font-weight: 500;
  line-height: 1.14286;
  text-decoration: none;
  transition: color .4s cubic-bezier(.4, 0, .2, 1);
}

.footer-navigation-link:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: var(--hover-color);
  transform-origin: 100% 100%;
  transition: transform var(--transition);
  border-radius: 3px;
  position: absolute;
  top: 25px;
  left: 0;
  transform: scaleX(0);
}

.footer-navigation-link:hover:after {
  transform-origin: 0 100%;
  transform: scaleX(1);
}

.footer-navigation-link:hover, .footer-navigation-link:focus {
  color: #fd9222;
}

@media screen and (min-width: 768px) {
  .footer {
    padding: 50px 0;
  }

  .footer-container {
    width: 704px;
  }

  .footer-section {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 50px;
    display: flex;
  }

  .addresses {
    gap: 50px;
  }

  .footer-title {
    font-size: 28px;
  }

  .footer-social-link {
    width: 24px;
    height: 24px;
  }

  .footer-line {
    margin-bottom: 24px;
  }

  .footer-navigation-list {
    gap: 24px;
    display: flex;
  }
}

@media only screen and (min-width: 1200px) {
  .footer-section {
    margin-bottom: 47px;
  }

  .footer-title {
    width: 300px;
    color: #fff;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.2;
  }

  .footer-container {
    width: 1136px;
  }
}

